import React from "react";

// 
function ParentComponent({ children }) {
  return (
    <div>
      <h3></h3>
      {children}
    </div>
  );
}

//  1
function Child1() {
  return (
    <div>
      <p>1</p>
    </div>
  );
}

//  2
function Child2() {
  return (
    <div>
      <p>2</p>
    </div>
  );
}

// children  props    children .
// children    (ParentComponent)     .
//            .
//   
export default function App() {
  return (
    <>
      <ParentComponent>
        <Child1 />
      </ParentComponent>

      <p>------------------------</p>

      <ParentComponent>
        <Child2 />
      </ParentComponent>
    </>
  );
}
