import React, { useState, useEffect } from "react";

export default function UseEffectSample() {
  const [count, setCount] = useState(0);

  //     useEffect
  useEffect(() => {
    console.log(" !");
  }, []);

  // count    useEffect
  useEffect(() => {
    console.log(` : ${count}`);
  }, [count]);

  return (
    <div>
      <p>  : {count}</p>
      <button onClick={() => setCount(count + 1)}></button>
    </div>
  );
}

//     100%   -        .
//       ,       .
//  ui      .

// useEffect  []   useEffect       .
//    ,  [data]   data       .
