import React, { useState } from "react";
import videoUrl from "../assets/videos/samplevideo.mp4";
import sgpUrl from "../assets/images/common/sgp.png";
import Popup from "./Popup";
import usePopup from "../hooks/usePopup";
import { useLanguageContext } from "../context/LanguageContext";

export default function Comp() {
  const { msgTxt } = useLanguageContext(); //Popup Language Number :  (all components)   
  const { show, isPopupOpen, popupName, msg, openPopup, closePopup } = usePopup(); //Custom Hook :   

  return (
    <>
      <h2 data-lang="OUTERPLANE">Hello world!</h2>
      <span className="logo"></span>
      <span
        className="banner"
        onClick={() => {
          openPopup("popAlert", msgTxt[3]);
        }}
      ></span>
      <span
        className="banner"
        onClick={() => {
          openPopup("popYoutube", "vvvvvvvv");
        }}
      ></span>
      <p>{true ? 1 : 0}</p>
      <p>{true && "NICE"}</p>
      <p>{false && "NICE"}</p>
      <img src={sgpUrl} alt="" width="100" />
      <video width="300" height="180" controls>
        <source src={videoUrl} type="video/mp4" />
      </video>
      {isPopupOpen && <Popup show={show} popupName={popupName} msg={msg} onClose={closePopup} />}
    </>
  );
}

